<template>
  <div>
    <el-container>
      <el-header>
        <HeaderTop />
      </el-header>
      <el-container>
        <el-aside width="VuexisCollapse?200px:64px">
          <asideNav />
        </el-aside>
        <div class="p-10" style="width: 100%">
          <div
            style="background: white; border: 1px solid #dbdfe999"
            class="p-20"
          >
            <el-breadcrumb separator="/">
              <el-breadcrumb-item
                v-for="(item, index) in breadcrumbItems"
                :key="index"
              >
                <span v-if="item.path">{{ item.meta.title }}</span>
                <span v-else>{{ item.meta.title }}</span>
              </el-breadcrumb-item>
            </el-breadcrumb>
          </div>
          <el-main>
            <router-view></router-view>
          </el-main>
        </div>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import HeaderTop from "@/components/header_top.vue";
import asideNav from "@/components/asideNav.vue";

export default {
  components: {
    HeaderTop,
    asideNav,
  },
  data() {
    return {
      breadcrumbItems: [],
    };
  },
  watch: {
    $route() {
      this.generateBreadcrumb();
    },
  },
  methods: {
    generateBreadcrumb() {
      let routes = this.$router.getRoutes().filter((route) => route.name);
      let currentRoute = routes.find(
        (route) => route.path === this.$route.path
      );
      let breadcrumbItems = [];
      let firstMeta = {
        title: "AIOTEC边缘计算网关",
      };

      while (currentRoute && currentRoute.meta) {
        breadcrumbItems.unshift({
          path: currentRoute.path,
          meta: currentRoute.meta.title ? currentRoute.meta : firstMeta,
        });
        currentRoute = currentRoute.parent || currentRoute.redirect;
      }

      this.breadcrumbItems = breadcrumbItems;
    },
  },
  created() {
    this.generateBreadcrumb();
  },
};
</script>

<style>
.el-tree--highlight-current .el-tree-node.is-current > .el-tree-node__content {
  background-color: aliceblue;
  color: #24aaff;
}
</style>
