<template>
  <el-menu
    :default-active="$route.name"
    class="el-menu-vertical-demo"
    @select="handleSelect"
  >
    <template v-for="route in filteredRoutes()">
      <!-- 如果有子路由，则生成子菜单 -->
      <el-submenu
        v-if="route.children && route.children.length > 0"
        :index="route.name"
        :key="route.path"
      >
        <template slot="title">
          <i :class="`icon iconf-${route.meta.icon}`"> </i>
          {{ route.meta.title }}</template
        >
        <template v-for="child in route.children">
          <el-menu-item
            :key="child.path"
            :index="child.name"
            v-if="!child.meta.hidden"
          >
            <i :class="`icon iconf-${child.meta.icon}`"> </i>
            <span class="text">{{ child.meta.title }}</span>
          </el-menu-item>
        </template>
      </el-submenu>
      <!-- 如果没有子路由，则生成普通菜单项 -->
      <el-menu-item v-else :key="route.path" :index="route.name">
        <i :class="`icon iconf-${route.meta.icon}`"> </i>
        <span class="text">{{ route.meta.title }}</span>
      </el-menu-item>
    </template>
  </el-menu>
</template>

<script>
export default {
  data() {
    return {
      routes: this.$router.options.routes[0].children, // 获取所有路由
    };
  },
  created() {},
  mounted() {},
  methods: {
    filteredRoutes() {
      return this.routes.filter(
        (route) => !route.hidden && route.meta && route.meta.title
      );
    },
    handleSelect(key) {
      this.$router.push({ name: key });
    },
  },
};
</script>
<style lang="less" scoped>
.icon {
  color: #2098e4;
  margin-right: 10px;
}
::v-deep(.el-menu-item.is-active) {
  background: aliceblue;
}
</style>
